.SignContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fafafa;
  flex-direction: column;
}
.SignContainer .SignInnerContainer {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fafafa;
  margin-top: 20px;
}

.SignContainer .SignInnerContainer .rightDiv {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 30px;
}
.SignContainer .SignInnerContainer .rightDiv .form {
  width: 300px;
  height: auto;
  padding: 20px;
  background-color: #fff;
  border: 1px solid #ccc;
}
.SignContainer .SignInnerContainer .rightDiv .form .form-group {
  width: 100%;
  padding: 2px;
  margin: 10px 0;
  display: flex;
  align-items: center;
  background-color: #fafafa;
  border: 1px solid #ccc;
  border-radius: 3px;
}
.SignContainer .SignInnerContainer .rightDiv .form .form-group .validation {
  width: 30px;
  height: 30px;
  opacity: 0.5;

  display: flex;
  align-items: center;
  justify-content: center;
}
.form-group .validation:not(.username) {
  pointer-events: none;
}
.SignContainer
  .SignInnerContainer
  .rightDiv
  .form
  .form-group
  .username.rotate {
  animation: 0.5s rot infinite linear;
}
@keyframes rot {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
.SignContainer .SignInnerContainer .rightDiv .form .form-group .validation img {
  width: 100%;
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.SignContainer .SignInnerContainer .rightDiv .form .form-group .show_and_hide {
  flex: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-weight: bold;
}
.SignContainer .SignInnerContainer .rightDiv .form .logo {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 47px;
  margin-bottom: 19px;
}
.SignContainer .SignInnerContainer .rightDiv .form .logo img {
  width: auto;
  height: 100%;
}
.SignContainer .SignInnerContainer .rightDiv .form p {
  text-align: center;
  color: #8e8e8e;
  font-size: 15px;
  margin-bottom: 10px;
  font-weight: bold;
}
.user-input-wrp {
  position: relative;
  flex: 7;
  height: 32px;
}
.user-input-wrp .inputText {
  width: 100%;
  height: 100%;
  outline: none;
  border: none;
  box-shadow: none !important;
  background-color: #fafafa;
  padding: 2px 10px;
  position: absolute;
  bottom: 0;
  left: 0;
}
.user-input-wrp .inputText:valid {
  border-color: #333;
  border-width: medium medium 1px;
  height: 50%;
}
.user-input-wrp .floating-label {
  position: absolute;
  pointer-events: none;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.2s ease all;
  font-size: small;
  top: 0;
  padding: 0 10px;
}

.user-input-wrp input:valid ~ .floating-label {
  height: 50%;
  font-size: 10px;
  opacity: 1;
}
.form-group input[type="submit"] {
  width: 100%;
  height: auto;
  padding: 10px;
  background-color: #ef3b3a;
  border: 0;
  outline: 0;
  cursor: pointer;
  border-radius: 3px;
  color: #fff;
  font-weight: bold;
}
.SignContainer
  .SignInnerContainer
  .rightDiv
  .form
  .form-group
  input[type="submit"]:disabled {
  background-color: #ef3a3ab4;
  cursor: not-allowed;
}
.SignContainer
  .SignInnerContainer
  .rightDiv
  .form
  .form-group
  input[type="submit"].loading {
  /* background-image: url("../../../public/loader.gif"); */
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-color: rgb(170, 215, 233);
  cursor: not-allowed;
}
.lineBox {
  width: 100%;
  height: auto;
  position: relative;
  display: block;
  padding: 12px 0;
}
.lineBox .line {
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #ccc;
  display: block;
}
.lineBox .or {
  padding: 5px;
  background-color: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translate(-50%, -50%);
}
.SignupFacebook {
  background-color: #0095f6;
  padding: 6px 0;
  margin: 5px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ForgotPassword {
  width: 100%;
  padding: 14px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.SignupFacebook a,
.ForgotPassword p {
  display: flex;
  justify-content: center;
  align-items: center;
}
.SignupFacebook a svg {
  color: #fff;
}
.SignupFacebook a span {
  margin-left: 10px;
  color: #fff;
}
.ForgotPassword p {
  font-size: small;
  color: #ccc;
  text-align: center;
}
.signUpBox {
  width: 300px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  border: 1px solid #ccc;
  margin-top: 15px;
}
.signUpBox p {
  margin: 0;
  font-weight: normal;
}
.signUpBox p a {
  color: #ef3b3a;
  font-weight: bold;
}
.getTheApp {
  width: 300px;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
.getTheApp p {
  text-align: center;
  margin: 10px 0;
}
.getTheApp .content {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.getTheApp .content img {
  width: 45%;
  height: 100%;
}
.information {
  width: 49%;
  height: auto;
  margin-bottom: 30px;
  text-align: center;
}
.information a {
  display: inline-block;
  margin: 5px 10px;
  font-size: 14px;
  color: #ccc;
  text-transform: capitalize;
}
@media (max-width: 770px) {
  .leftDiv {
    display: none;
  }
  .rightDiv {
    width: 100%;

    justify-content: center;
    align-items: center;
  }
  .form {
    width: 305px;
  }
  .signUpBox {
    width: 305px;
  }
  .getTheApp {
    width: 305px;
  }
  .getTheApp {
    width: 305px;
  }
}
